import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { RequestService } from 'src/app/api/request.service';
import { DialogService } from 'src/app/api/util/dialog.service';
import { ModalController, NavParams, IonImg } from '@ionic/angular';
import { PhotoService } from 'src/app/services/photo.service';
import { Model } from 'src/app/api/models/model';
import { ModelImage } from 'src/app/api/models/modelImage';
import { AuthUserService } from 'src/app/services/auth-user.service';
import { PathsImagesService } from 'src/app/services/paths-images.service';
import { ModalForm } from 'src/app/models/modal-form';
import { ModalFormInterface } from 'src/app/interfaces/modal-form-interface';
import { PlatformLocation } from '@angular/common';
import { ChatService } from 'src/app/services/chat.service';

@Component({
  selector: 'app-form-publication',
  templateUrl: './form-publication.page.html',
  styleUrls: ['./form-publication.page.scss'],
})
export class FormPublicationPage extends ModalForm implements OnInit , ModalFormInterface {


  @ViewChild('form_c') form_c: ElementRef ;
  @ViewChild('img_publication') img_publication: ElementRef ;



  public id: any;
  public publication : any;
  public servicePublication : Model;
  public photoModel : ModelImage;
  public user : any;
  public posting = false;

  constructor(
    public request : RequestService,
    public dialogService : DialogService,
    public viewCtrl: ModalController,
    public photoService : PhotoService,
    navParams: NavParams,
    public authUser : AuthUserService,
    public pathImages : PathsImagesService,
    location: PlatformLocation,
    public chatService : ChatService
  ) {
    super(location,viewCtrl);
    this.photoModel = new ModelImage(request,viewCtrl);
    this.photoModel.init('images/publications/',false)
    this.photoModel.setAspectRatioCrop('none');
    this.servicePublication = new Model('Publication',request);
    //this.serviceTeam.setModel('Team');
    console.log('user auth' , authUser.user)

    this.id = navParams.get('id') ;
    
   }

  initObject(){

    this.photoModel.setElementImg('img_publication');

    this.user = this.authUser.user //await this.authUser.getUser();
    console.log('user' , this.user)

    this.publication = { 
      text : '',
      user_id : this.user.id
    };

    if(this.id){
      this.servicePublication.api_find(this.id).subscribe(data => {
        if(data['status'] == 'success'){
          this.publication = data['Publication'];
          if(this.publication.image){
            this.photoModel.setImage(this.publication.image);
          }
        }
      })
    }
  }



  submit(){

    try{
      this.validSend();
    }catch(e){
      this.dialogService.showToast(e.message,null,'danger');
      return;
    }

    if(this.photoModel.isLoadPthoto()){
      this.publication.image = this.photoModel.getImageUpload();
    }

    this.posting = true;
    if(this.publication.id){
      this.servicePublication.api_update(this.publication).subscribe(
        data => {
          if(data['status'] == 'success'){
            this.dialogService.showToast( data['msjSuccess'] ?? 'Registrado' );
            console.log(data);
            this.dismiss(data['Publication'])
          }
          this.posting = false;
        },
        error => {
          this.posting = false;
        });

    }else{
      this.servicePublication.api_create(this.publication).subscribe(
        data => {
          if(data['status'] == 'success'){
            this.dialogService.showToast( data['msjSuccess'] ?? 'Registrado' );
            this.dismiss(data['Publication'])
          }
          this.posting = false;

        },
        error => {
          this.posting = false;

        })
    }
  }

  validSend(){
    console.log('valid')
    if(!this.photoModel.isLoadPthoto() && this.publication.text.length == 0){
      throw new Error('Debe escribir algo en la publicaci√≥n o a√±adir una imagen.');
    }
  }

  dismiss(publication) {
    this.viewCtrl.dismiss(
      {
        publication : publication,
      }
    );
  }

  close(){
    this.viewCtrl.dismiss();
  }

  myDateFilter = (d: Date): boolean => {
    const day = d.getDay();
    return day == 6 ;
  }

  print(){
    console.log(this.publication);
  }


}
